.box-open {
    display: flex;
    justify-content: center;
    padding: 140px 0;
    margin: 60px 0;
    background-color: rgb(231, 231, 231);
    filter: drop-shadow(0px 3px 3px rgba(0, 0, 0, 0.25));
    box-shadow: 0px 2px 2px rgba(31, 30, 30, 0.25);
}

#box-img-open img{
    width: 400px;
    
}

#box-title-open {
    width: 600px;
    margin-right: 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

#box-title-open h2 {
    font-family: 'Playfair Display', serif;
    font-weight: 700;
    font-size: 62px;
    text-align: center;
    margin-top: 50px;
}

#box-title-open p {
    margin-top: 20px;
    text-align: center;
}

.btn-open {
    display: flex;
    transition: all 0.2s ease-in;
    position: relative;
    overflow: hidden;
    z-index: 1;
    color: #090909;
    padding: 0.7em 1.7em;
    font-size: 14px;
    border-radius: 0.8em;
    background: #e8e8e8;
    border: 1px solid #e8e8e8;
    box-shadow: 6px 6px 12px #c5c5c5,
        -6px -6px 12px #ffffff;
}

.btn-open:active {
    color: #666;
    box-shadow: inset 4px 4px 12px #c5c5c5,
        inset -4px -4px 12px #ffffff;
}

.btn-open:before {
    content: "";
    position: absolute;
    left: 50%;
    transform: translateX(-50%) scaleY(1) scaleX(1.25);
    top: 100%;
    width: 140%;
    height: 180%;
    background-color: rgba(0, 0, 0, 0.05);
    border-radius: 50%;
    display: block;
    transition: all 0.5s 0.1s cubic-bezier(0.55, 0, 0.1, 1);
    z-index: -1;
}

.btn-open:after {
    content: "";
    position: absolute;
    left: 55%;
    transform: translateX(-50%) scaleY(1) scaleX(1.45);
    top: 180%;
    width: 160%;
    height: 190%;
    background-color: #ffbadb;
    border-radius: 50%;
    display: block;
    transition: all 0.5s 0.1s cubic-bezier(0.55, 0, 0.1, 1);
    z-index: -1;
}

.btn-open:hover {
    color: #ffffff;
    border: 1px solid #ffbadb;
}

.btn-open:hover:before {
    top: -35%;
    background-color: #ffbadb;
    transform: translateX(-50%) scaleY(1.3) scaleX(0.8);
}

.btn-open:hover:after {
    top: -45%;
    background-color: #ffbadb;
    transform: translateX(-50%) scaleY(1.3) scaleX(0.8);
}

